<template>
  <div class="box">
    <g-button @click="showToast">toast</g-button>
  </div>

</template>

<script>
export default {
  methods: {
    showToast() {
      this.$toast("<p>message</p>", {
        position: "top",
        enableHtml: true,
        closeButton: {
          text: "",
          callback(toast) {
            toast.log();
            console.log("");
          }
        }
      });
    }
  }
}
</script>