<!-- Table -->
<template>
  <g-table
    :data-source="dataSource"
    :selected-items.sync="selected"
    :bordered="true"
    :order-by.sync="orderBy"
    @update:orderBy="x"
    :loading="loading"
    :height="400"
    expend-field="description"
    :checkable="true"
  >
    <template slot-scope="xxx">
      <button @click="edit(xxx.item)"></button><button @click="view(xxx.item)"></button>
    </template>

    <g-table-column text="" field="name" :width="100">
      <template slot-scope="props">
        <a href="#">{{props.value}}</a>
      </template>
    </g-table-column>
    <g-table-column text="" field="score"></g-table-column>

  </g-table>
</template>

<script>
import GTable from '@/components/table/table.vue'
import GTableColumn from '@/components/table/table-column.vue'

export default {
  data() {
    return {
      selected: [{
        id: 1, name: '', score: 100
      }],
      // columns: [
      //   { text: '', field: 'name', width: 200 },
      //   { text: '', field: 'score' }
      // ],
      orderBy: { // trueasc desc
        name: true,
        score: 'desc'
      },
      loading: false,
      dataSource: [
        {
          id: 1, name: '', score: 100, description: ''
        },
        {
          id: 2, name: '', score: 37, description: ''
        },
        {
          id: 3, name: '', score: 45, description: ''
        },
        {
          id: 4, name: '', score: 99, description: ''
        },
        {
          id: 5, name: '', score: 25, description: ''
        },
        {
          id: 6, name: '', score: 89, description: ''
        },
        {
          id: 7, name: '', score: 38, description: ''
        },
        {
          id: 8, name: '', score: 2, description: ''
        },
        {
          id: 9, name: '', score: 100, description: ''
        },
        {
          id: 10, name: '', score: 37, description: ''
        },
        {
          id: 11, name: '', score: 45, description: ''
        },
        {
          id: 12, name: '', score: 99, description: ''
        },
        {
          id: 13, name: '', score: 25, description: ''
        },
        {
          id: 14, name: '', score: 89, description: ''
        },
        {
          id: 15, name: '', score: 38, description: ''
        },
        {
          id: 16, name: '', score: 2, description: ''
        }
      ]
    };
  },

  components: {
    GTable,
    GTableColumn
  },
  methods: {
    x(key) {
      //  this.loading = true
      this.dataSource = this.dataSource.sort((a, b) => a.score - b.score)
    },
    edit(item) {
      alert(`${item.id}`)
    },
    view(item) {
      alert(`${item.id}`)
    }
  }
}

</script>
